// Portfolio Block Index Page
//-----------------------------------
.portfolio {
  .page-section {
    padding: 0;
  }
  .section-ttl-block {
    line-height: 1;
    margin-bottom: 0;
  }
  .item {
    position: relative;
    z-index: 1;
    display: block;
    margin: 0;
    overflow: hidden;
    margin: 20px;
    cursor: pointer;
  }

  &__ttl {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
  }

  &__list {
    & > span {
      overflow: hidden;
    }
    .bar {
      display: block;
      position: relative;
      height: 24px;
      background-color: #ddd;
      width: 100%;
      color: #6a6a6a;
      z-index: 10;
      h2, h3 {
        content: attr(data-text);
        text-align: center;
        font-size: 1.05rem;
        text-transform: uppercase;
        font-family: Arial, sans-serif;
        font-weight: 700;
        line-height: 26px;
        letter-spacing: -0.5px;
        color: #8a8a8a;
      }
      i, i:after, i:before {
        height: 10px;
        width: 10px;
        display: inline-block;
        background-color: #ff5f57;
        border-radius: 50%;
        position: absolute;
        top: 7px;
        left: 5px;
      }
      i {
        &:before {
          content: "";
          left: 13px;
          background-color: #ffbd2e;
          top: 0;
        }
        &:after {
          content: "";
          left: 26px;
          background-color: #28ca41;
          top: 0;
        }
      }
    }
    .main {
      background-size: cover;
      padding-top: 50%;
      position: relative;
      background-position: center center;
      display: block;
      width: 100%;
      border-radius: 0;
      -moz-transform-style: flat;
      -webkit-transform-style: flat;
      transform-style: flat;
      &:before {
        content: "";
        position: absolute;
        z-index: 20;
        box-shadow: 0 0 50px rgba(0, 0, 0, 0.75) inset;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
    .back {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
    }
    .tags {
      opacity: 1;
      position: absolute;
      text-align: left;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 8;
      display: block;
      div {
        display: block;
        height: 100%;
        margin: 0 auto;
        width: 80%;
        position: relative;
        display: -webkit-flex;
        -webkit-align-items: center;
        display: flex;
        align-items: center;
      }
      ul {
        display: block;
        text-align: center;
        position: relative;
        -moz-transform-style: preserve-3d;
        -webkit-transform-style: preserve-3d;
        transform-style: preserve-3d;
        margin: 0 auto;
      }
      li {
        display: inline-block;
        padding: 0 0.5em;
        background-color: #313131;
        color: #999;
        border-width: 1px 1px 2px 1px;
        border-style: solid;
        border-color: #222;
        border-radius: 3px;
        text-transform: uppercase;
        font-family: MontSerrat, Tahoma, Arial, sans-serif;
        font-weight: 700;
        font-size: 1.2rem;
        box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.25);
        margin: 2px;
        opacity: 0;
        position: relative;
        text-shadow: -1px -1px 0 #3f3f3f, -1px 0 0 #3f3f3f, -1px 1px 0 #3f3f3f,
          0 -1px 0 #3f3f3f, 0 0 0 #3f3f3f, 0 1px 0 #3f3f3f, 1px -1px 0 #3f3f3f,
          1px 0 0 #3f3f3f, 1px 1px 0 #3f3f3f;
        -moz-backface-visibility: hidden;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden;
        -moz-transform-origin: 50% 50% -10px;
        -webkit-transform-origin: 50% 50% -10px;
        transform-origin: 50% 50% -10px;
        -moz-transform-style: flat;
        -webkit-transform-style: flat;
        transform-style: flat;
        -moz-transform: rotateX(92deg);
        -ms-transform: rotateX(92deg);
        -webkit-transform: rotateX(92deg);
        transform: rotateX(92deg);
        -moz-transition: -moz-transform 0.285s cubic-bezier(0.5, 1.5, 0.5, 1.5);
        -o-transition: -o-transform 0.285s cubic-bezier(0.5, 1.5, 0.5, 1.5);
        -webkit-transition: -webkit-transform 0.285s cubic-bezier(0.5, 1.5, 0.5, 1.5);
        transition: transform 0.285s cubic-bezier(0.5, 1.5, 0.5, 1.5);

        $sum : 0;
        @for $i from 1 through 6 {
          $sum : $sum + 0.1s;
          &:nth-child(#{$i}) {
            transition-delay: $sum, $sum;
            z-index: 13 - $i;
          }
        }
      }
    }
    .item {
      &:hover {
        .tags {
          li {
            opacity: 1;
            -moz-transform: translate3d(0, 0, 0) rotateX(0);
            -ms-transform: translate3d(0, 0, 0) rotateX(0);
            -webkit-transform: translate3d(0, 0, 0) rotateX(0);
            transform: translate3d(0, 0, 0) rotateX(0);
          }
        }
        .imgs {
          opacity: 1;
          transform: translate3d(0, 0, 0);
          visibility: visible;
        }
      }
      &:hover 
      &:hover .tags li,
      &:hover .tags:before,
      &:hover h2 {
        -moz-transition-delay: 0s, 0s;
        -o-transition-delay: 0s, 0s;
        -webkit-transition-delay: 0s, 0s;
        transition-delay: 0s, 0s;
      }
    }
    .imgs {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      display: block;
    }
    .imgs {
      left: 0;
      width: calc(100%);
      border-radius: 0;
      opacity: 0;
      visibility: hidden;
      z-index: 3;
      overflow: hidden;
      -moz-transform: translate3d(0, -100%, 0);
      -ms-transform: translate3d(0, -100%, 0);
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      -moz-transform-style: flat;
      -webkit-transform-style: flat;
      transform-style: flat;
      -moz-transition: opacity 0.15s ease-out,
        -moz-transform 0.25s cubic-bezier(0, 0.75, 0, 1);
      -o-transition: opacity 0.15s ease-out,
        -o-transform 0.25s cubic-bezier(0, 0.75, 0, 1);
      -webkit-transition: opacity 0.15s ease-out,
        -webkit-transform 0.25s cubic-bezier(0, 0.75, 0, 1);
      transition: opacity 0.15s ease-out,
        transform 0.25s cubic-bezier(0, 0.75, 0, 1);
        &:before {
          content: "";
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          background-color: rgba(10, 10, 10, 0.7);
          background-blend-mode: luminosity;
        }
    }
  }
  &__pro-list {
    position: relative;
    .pro-list__item {
      display: block;
      margin: 15px 16px;
      // cursor: pointer;
      .screen {
        position: relative;
        display: block;
        width: 100%;
        height: 180px;
        overflow: hidden;
        border: 1px solid #f0f0f0;
        box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
        &-img {
          transform: translate3d(0px, 0px, 0px);
        }
      }
      &:hover {
        .screen {
          &-img {
            transform: translate3d(0px, calc(-100% + 180px), 0px);
          }
        }
      }

      .pro-name {
        margin: 20px 0 0;
        text-align: center;
        font-family: 'Poppins', sans-serif;
        font-weight: bold;
        font-size: 2rem;
      }
    }
    @include mediaQuery("md") {
      .pro-list__item {
        margin: 10px 0;
        .screen {
          height: 200px;
        }
        &:hover {
          .screen {
            &-img {
              transform: translate3d(0px, calc(-100% + 200px), 0px);
            }
          }
        }
      }
    }
  }

  &__more {
    text-align: center;
  }

  @include mediaQuery("md") {
    .section-ttl-block {
      margin-bottom: 30px;
    }
    &__more {
      font-size: 1.2rem;
      margin-top: 30px;
      .button {
        width: 90%;
        .icon-right {
          &:after {
            top: 22px;
          }
        }
      }
    }
  }
}

// Projects Page
//-----------------------------------
.projects {
  
  .page-section {
    margin-left: 0;
  }
  .section-ttl-block {
    margin-bottom: 50px;
  }
  @include mediaQuery("md") {
    padding: 0;
  }
}