  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(10, 10, 10, 0.7);
  background-blend-mode: luminosity;
}

.portfolio__pro-list {
  position: relative;
}

.portfolio__pro-list .pro-list__item {
  display: block;
  margin: 15px 16px;
}

.portfolio__pro-list .pro-list__item .screen {
  position: relative;
  display: block;
  width: 100%;
  height: 180px;
  overflow: hidden;
  border: 1px solid #f0f0f0;
  box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
}

.portfolio__pro-list .pro-list__item .screen-img {
  transform: translate3d(0px, 0px, 0px);
}

.portfolio__pro-list .pro-list__item:hover .screen-img {
  transform: translate3d(0px, calc(-100% + 180px), 0px);
}

.portfolio__pro-list .pro-list__item .pro-name {
  margin: 20px 0 0;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
  font-size: 2rem;
}

@media screen and (max-width: 768px) {
  .portfolio__pro-list .pro-list__item {
    margin: 10px 0;
  }
  .portfolio__pro-list .pro-list__item .screen {
    height: 200px;
  }
  .portfolio__pro-list .pro-list__item:hover .screen-img {
    transform: translate3d(0px, calc(-100% + 200px), 0px);
  }
}

.portfolio__more {
  text-align: center;
}

@media screen and (max-width: 768px) {
  .portfolio .section-ttl-block {
    margin-bottom: 30px;
  }
  .portfolio__more {
    font-size: 1.2rem;
    margin-top: 30px;
  }
  .portfolio__more .button {
    width: 90%;
  }
  .portfolio__more .button .icon-right:after {
    top: 22px;
  }
}

.projects .page-section {
  margin-left: 0;
}

.projects .section-ttl-block {
  margin-bottom: 50px;
}

@media screen and (max-width: 768px) {
  .projects {
    padding: 0;
  }
}

#detail {
  background: #fff5f6;
  padding-bottom: 50px;
}

.detail {
  width: 100%;
  font-family: "Lato";
  padding: 30px 50px;
  background: #ffffff;
  border-radius: 30px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.16);
}

.detail__name {
  color: #1d1d1d;
  font-size: 4rem;
  line-height: 1.5;
  font-weight: 700;
  letter-spacing: -2px;
  margin-bottom: 0.5em;
  width: 100%;
  text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.35);
  text-align: center;
}

.detail__share {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  margin-bottom: 20px;
}

.detail__info {
  margin-bottom: 3em;
  text-align: left;
}

.detail__info h2 {
  font-size: 2rem;
  line-height: 1.5;
  font-weight: 600;
  position: relative;
  display: block;
  letter-spacing: -.045em;
  padding: 0;
  margin-bottom: .4em;
}

.detail__info hr {
  display: block;
  height: 1px;
  width: 50%;
  background-color: #ccc;
  position: relative;
  border: 0;
  margin: .5em 0 1em;
}

.detail__keywords {
  list-style: circle;
  padding: 0 1.4em;
  margin-bottom: .6em;
}

.detail__keywords li {
  font-size: 1.6rem;
  line-height: 1.5;
}

.detail .btn-1 {
  color: #666666;
  margin: 0;
  background: #ffffff;
}

.detail .btn-1::before {
  border-bottom-color: #666666;
}

.detail .btn-1:hover {
  color: #ffffff;
}

.detail .btn-1:hover path {
  fill: #ffffff;
}

@media screen and (max-width: 768px) {
  .detail {
    padding: 20px 20px;
  }
  .detail-block {
    padding: 0;
  }
  .detail__name {
    font-size: 2.8rem;
  }
  .detail__share {
    display: block;
  }
  .detail__share a {
    width: 100%;
  }
  .detail__keywords li {
    font-size: 1.5rem;
  }
}

.slick-slide img {
  margin: 0 auto;
}

.slick-dots {
  bottom: -30px;
}

.slick-dots li button:before {
  color: #666666;
  opacity: 1;
  font-size: 1rem;
}

.slick-dots li.slick-active button:before {
  color: #666666;
  font-size: 1.8rem;
  opacity: 1;
}

.slick-dotted.slick-slider {
  margin-bottom: 70px;
}

.slick-prev,
.slick-next {
  width: 80px;
  height: 40px;
  padding: 0 30px;
  z-index: 10;
}

.slick-prev:before,
.slick-next:before {
  content: "";
}

.slick-prev span:before,
.slick-prev span:after,
.slick-next span:before,
.slick-next span:after {
  content: "";
  position: absolute;
  background-color: #666666;
  transition: transform 0.15s ease-out, background-color 0.15s ease-out;
  width: 4px;
  height: 100%;
  z-index: 10;
  opacity: 0.8;
}

.slick-prev span:before {
  transform-origin: 0 100% 0;
  top: -50%;
  transform: rotate(45deg);
}

.slick-prev span:after {
  transform-origin: 0 0 0;
  top: 50%;
  transform: rotate(-45deg);
}

.slick-next span:before {
  transform-origin: 100% 100% 0;
  top: -50%;
  transform: rotate(-45deg);
}

.slick-next span:after {
  transform-origin: 100% 0% 0;
  top: 50%;
  transform: rotate(45deg);
}

.slick-prev:hover span::before {
  opacity: 1;
  transform: rotate(30deg);
}

.slick-prev:hover span::after {
  opacity: 1;
  transform: rotate(-30deg);
}

.slick-next:hover span::before {
  opacity: 1;
  transform: rotate(-30deg);
}

.slick-next:hover span::after {
  opacity: 1;
  transform: rotate(30deg);
}

@media screen and (max-width: 768px) {
  .slick-prev,
  .slick-next {
    width: 50px;
    height: 25px;
    padding: 0 15px;
    z-index: 10;
  }
  .slick-prev span:before, .slick-prev span:after, .slick-next span:before, .slick-next span:after {
    width: 3px;
  }
  .slick-prev {
    left: -15px;
  }
  .slick-next {
    right: -15px;
  }
}

.contact {
  position: relative;
  text-align: left;
  font-size: 1.8rem;
  overflow: hidden;
  background-image: url("/images/contact-bg.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
}

.contact::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(42, 42, 42, 0.4);
}

.contact .section-ttl-block {
  margin-bottom: 20px;
}

.contact .container {
  position: relative;
  z-index: 1;
}

@media screen and (min-width: 768px) {
  .contact .container {
    margin-top: 80px;
  }
}

.contact h3 {
  color: #cacaca;
  font-size: 2.6rem;
  line-height: 1.5;
  font-weight: 700;
  letter-spacing: -2px;
  margin-bottom: 0.1em;
  width: 100%;
  text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.15);
}

.contact a {
  font-weight: bold;
}

.contact__form {
  margin-top: 20px;
}

.contact__form ul {
  display: flex;
  flex-wrap: wrap;
}

.contact__form ul li {
  position: relative;
  width: 100%;
  margin-bottom: 10px;
  overflow: hidden;
}

.contact__form ul li.half {
  width: 49%;
  margin-left: 2%;
}

.contact__form ul li:first-child {
  margin-left: 0;
}

.contact__form ul textarea {
  width: 100%;
  border: 0;
  background: #4d4d4d;
  height: 50px;
  font-size: 1.6rem;
  color: #ffffff;
  padding: 20px;
  box-sizing: border-box;
  min-height: 150px;
}

.contact__form .flat-button {
  color: #e31b6d;
  font-size: 1.1rem;
  letter-spacing: 3px;
  text-decoration: none;
  padding: 8px 10px;
  border: 1px solid #e31b6d;
  border-radius: 4px;
  background: 0 0;
  text-transform: uppercase;
  float: right;
  text-align: center;
  cursor: pointer;
}

.contact__form .flat-button:hover {
  color: #1d1d1d;
  background: #e31b6d;
}

.contact__socical {
  display: flex;
  flex-wrap: wrap;
}

.contact__socical li {
  margin-right: 1em;
}

.contact .input__field {
  width: 100%;
  border: 0;
  background: #4d4d4d;
  height: 50px;
  font-size: 1.6rem;
  color: #ffffff;
  padding: 0 20px;
  box-sizing: border-box;
}

.contact .input__field--hoshi:focus + .input__label--hoshi:after {
  transform: translateX(0);
}

.contact .input__label--hoshi::before, .contact .input__label--hoshi::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
}

.contact .input__label--hoshi::after {
  margin-top: 2px;
  border-bottom: 2px solid #e31b6d;
  transform: translateX(-100%);
  transition: transform 0.3s;
}

@media screen and (max-width: 768px) {
  .contact {
    padding: 4em 0 3em;
  }
  .contact li {
    width: 100%;
    margin-right: 0;
  }
  .contact li a {
    width: 100%;
  }
}
